import { Component, OnInit } from '@angular/core';
import {
  LoincObject,
  LoincObjectGroup,
  LoincObjectProperty,
  LoincTableService,
} from '../../../../../shared/services/loinc.service';
import { ActivatedRoute, Params } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { API_BASE_LOINC } from '../../../../../shared/common';


interface HeaderField {
  number?: LoincObjectProperty;
  long_common_name?: LoincObjectProperty;
  status?: LoincObjectProperty;
}

@Component({
  selector: 'app-loinc-table',
  templateUrl: './loinc-table.component.html',
  styleUrls: ['./loinc-table.component.scss']
})
export class LoincTableComponent implements OnInit {
  private destroy$ = new Subject<void>();
  singleResult:  LoincObjectGroup = {};
  headerFields: HeaderField = {};
  id: string = '';
  key: string = 'number';
  language: string = localStorage.getItem('lang') ?? 'vi';

  constructor(
    public loincTableService: LoincTableService,
    private route: ActivatedRoute
  ) {
    this.route.params.pipe(takeUntil(this.destroy$)).subscribe((params: Params) => {
      this.id = params['id'];
      if (this.id) {
        this.getDetail();
      }
      else {
        this.singleResult = {}
      }
    })
  }

  ngOnInit(): void {}

  getDetail() {
    this.loincTableService.retrieve(this.id).subscribe((data) => {
      this.singleResult = data;
      this.headerFields = this.get_header_fields();
    })
  }

  isDetailView(): boolean {
    return Object.keys(this.singleResult).length > 0;
  }

  get_header_fields(): HeaderField {
    let fields: LoincObject = {};
    let headerKeys = ['number', 'long_common_name', 'status']

    Object.entries(this.singleResult).forEach(([key, value]: [string, LoincObject]) => {
      Object.entries(value).forEach(([key, value]: [string, LoincObjectProperty]) => {
        if (headerKeys.includes(key)) {
          if (!value.value) {
            value.value = '-';
          }
          fields[key] = value;
        }
      })
    })

    return {
      number: fields['number'],
      long_common_name: fields['long_common_name'],
      status: fields['status'],
    };
  }

  getFeedbackURL() {
    return `${API_BASE_LOINC}/${this.language}/admin/loinc_table/loinctable/${this.id}/change/`
  }
}
