import { Component, OnInit } from '@angular/core';
@Component({
  selector: 'app-icd10v3',
  templateUrl: './icd10v3.component.html',
  styleUrls: ['./icd10v3.component.scss']
})
export class Icd10v3Component implements OnInit {
  searchKeyword: string = '';
  selectedAction: string = '';
  selectedCount: number = 0;
  totalCount: number = 0;
  selectAll: boolean = false;
  actionMessage: string = '';
  rows: { id: number; name: string; details: string; selected: boolean }[] = [];
  filteredRows: { id: number; name: string; details: string; selected: boolean }[] = [];
  displayedRows: { id: number; name: string; details: string; selected: boolean }[] = [];
  newName: string = '';
  newDetails: string = '';
  editingRow: { id: number; name: string; details: string } | null = null;
  currentPage: number = 1;
  rowsPerPage: number = 10;
  totalPages: number = 0;

  ngOnInit(): void {
    this.rows = [
      { id: 1, name: 'Bnh l 1', details: 'Chi tit bnh l 1', selected: false },
      { id: 2, name: 'Bnh l 2', details: 'Chi tit bnh l 2', selected: false },
      { id: 3, name: 'Bnh l 3', details: 'Chi tit bnh l 3', selected: false },
      { id: 4, name: 'Bnh l 4', details: 'Chi tit bnh l 4', selected: false },
      { id: 5, name: 'Bnh l 5', details: 'Chi tit bnh l 5', selected: false },
      { id: 6, name: 'Bnh l 6', details: 'Chi tit bnh l 6', selected: false },
      { id: 7, name: 'Bnh l 7', details: 'Chi tit bnh l 7', selected: false },
      { id: 8, name: 'Bnh l 8', details: 'Chi tit bnh l 8', selected: false },
      { id: 9, name: 'Bnh l 9', details: 'Chi tit bnh l 9', selected: false },
      { id: 10, name: 'Bnh l 10', details: 'Chi tit bnh l 10', selected: false },
      { id: 11, name: 'Bnh l 11', details: 'Chi tit bnh l 11', selected: false },
      { id: 12, name: 'Bnh l 12', details: 'Chi tit bnh l 12', selected: false },
      { id: 13, name: 'Bnh l 13', details: 'Chi tit bnh l 13', selected: false },
      { id: 14, name: 'Bnh l 14', details: 'Chi tit bnh l 14', selected: false },
      { id: 15, name: 'Bnh l 15', details: 'Chi tit bnh l 15', selected: false },
      { id: 16, name: 'Bnh l 16', details: 'Chi tit bnh l 16', selected: false },
      { id: 17, name: 'Bnh l 17', details: 'Chi tit bnh l 17', selected: false },
      { id: 18, name: 'Bnh l 18', details: 'Chi tit bnh l 18', selected: false },
      { id: 19, name: 'Bnh l 19', details: 'Chi tit bnh l 19', selected: false },
      { id: 20, name: 'Bnh l 20', details: 'Chi tit bnh l 20', selected: false },
    ];
    this.filteredRows = [...this.rows];
    this.totalCount = this.filteredRows.length;
    this.totalPages = Math.ceil(this.totalCount / this.rowsPerPage);
    this.updateDisplayedRows();
  }

  exportFile(): void {
    const selectedRows = this.filteredRows.filter(row => row.selected);
    if (selectedRows.length === 0) {
      this.actionMessage = 'Vui lng chn t nht mt bnh l  xut!';
      return;
    }
    const csvData = this.convertToCSV(selectedRows);
    const blob = new Blob([csvData], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = 'selected_rows.csv';
    link.click();
    this.actionMessage = 'Xut file thnh cng!';
  }

  convertToCSV(rows: { id: number; name: string; details: string; selected: boolean }[]): string {
    if (!rows || rows.length === 0) {
      console.error('No rows to convert to CSV');
      return '';
    }
    const header = 'ID,Name,Details\n';
    const rowsData = rows.map(row => `${row.id},${row.name},${row.details}`).join('\n');
    return header + rowsData;
  }

  onSearch(): void {
    if (this.searchKeyword.trim()) {
      this.filteredRows = this.rows.filter(
        row =>
          row.name.toLowerCase().includes(this.searchKeyword.toLowerCase()) ||
          row.details.toLowerCase().includes(this.searchKeyword.toLowerCase())
      );
    } else {
      this.filteredRows = [...this.rows];
    }
    this.totalCount = this.filteredRows.length;
    this.totalPages = Math.ceil(this.totalCount / this.rowsPerPage);
    this.currentPage = 1;
    this.updateDisplayedRows();
  }

  performAction(): void {
    switch (this.selectedAction) {
      case 'add':
        this.addRow();
        break;
      case 'edit':
        this.editRow();
        break;
      case 'delete':
        this.deleteRows();
        break;
      case 'export':
        this.exportFile();
        break;
      default:
        this.actionMessage = 'Chn hnh ng hp l';
        break;
    }
  }

  addRow(): void {
    if (this.newName && this.newDetails) {
      const newRow = { 
        id: this.rows.length + 1, 
        name: this.newName, 
        details: this.newDetails, 
        selected: false 
      };
      this.rows.push(newRow);
      this.filteredRows = [...this.rows];
      this.totalCount = this.rows.length;
      this.totalPages = Math.ceil(this.totalCount / this.rowsPerPage);
      this.updateDisplayedRows();
      this.actionMessage = 'Thm bnh l thnh cng!';
      this.resetForm();
    } else {
      this.actionMessage = 'Vui lng nhp tn v chi tit bnh l!';
    }
  }

  editRow(): void {
    const selectedRows = this.filteredRows.filter(row => row.selected);
    if (selectedRows.length === 1) {
      const row = selectedRows[0];
      if (this.newName && this.newDetails) {
        row.name = this.newName;
        row.details = this.newDetails;
        this.actionMessage = 'Chnh sa bnh l thnh cng!';
        this.resetForm();
      } else {
        this.actionMessage = 'Vui lng nhp tn v chi tit bnh l!';
      }
    } else {
      this.actionMessage = 'Vui lng chn mt bnh l  chnh sa.';
    }
  }

  deleteRows(): void {
    const selectedRows = this.filteredRows.filter(row => row.selected);
    if (selectedRows.length > 0) {
      this.rows = this.rows.filter(row => !row.selected);
      this.filteredRows = this.filteredRows.filter(row => !row.selected);
      this.totalCount = this.rows.length;
      this.totalPages = Math.ceil(this.totalCount / this.rowsPerPage);
      this.updateDisplayedRows();
      this.actionMessage = 'Xa bnh l thnh cng!';
    } else {
      this.actionMessage = 'Vui lng chn t nht mt bnh l  xa.';
    }
  }

  updateSelectedCount(): void {
    this.selectedCount = this.filteredRows.filter((row) => row.selected).length;
  }

  toggleSelectAll(): void {
    this.filteredRows.forEach((row) => (row.selected = this.selectAll));
    this.updateSelectedCount();
  }

  resetForm(): void {
    this.newName = '';
    this.newDetails = '';
    this.editingRow = null;
  }

  startEditing(row: { id: number; name: string; details: string }): void {
    this.newName = row.name;
    this.newDetails = row.details;
    this.editingRow = row;
  }

  updateDisplayedRows(): void {
    const start = (this.currentPage - 1) * this.rowsPerPage;
    const end = start + this.rowsPerPage;
    this.displayedRows = this.filteredRows.slice(start, end);
  }

  goToPage(page: number): void {
    if (page > 0 && page <= this.totalPages) {
      this.currentPage = page;
      this.updateDisplayedRows();
    }
  }

  nextPage(): void {
    if (this.currentPage < this.totalPages) {
      this.currentPage++;
      this.updateDisplayedRows();
    }
  }

  prevPage(): void {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.updateDisplayedRows();
    }
  }


  userFeedback: string = '';
feedbackMessage: string = '';

submitFeedback() {
    if (this.userFeedback.trim()) {
        
        this.feedbackMessage = 'Cm n bn  gi gp !';
        this.userFeedback = ''; 
    } else {
        this.feedbackMessage = 'Vui lng nhp ni dung gp .';
    }

    
    setTimeout(() => {
        this.feedbackMessage = '';
    }, 3000);
}

}
